body {
  display: flex;
  flex-flow: column;
  align-items: center;
  height: 100%;
  width: 100%;

  /* ~~~~ FOR DEBUGGING~~~~*/
  /* border: 4px solid black; */
}

header {
  background-image: url(../imgs/main2_edited.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;

  width: 100%;
  height: 100vh;

  /* ~~~~ FOR DEBUGGING~~~~*/
  /* border: 4px solid rgb(177, 20, 143); */
}

.menu {
  display: flex;
  flex-flow: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 60px;
  position: absolute;
  top: -5px;

  /* ~~~~ FOR DEBUGGING~~~~*/
  /* border: 2px solid rgb(20, 177, 35); */
}

.menu.sticky {
  display: flex;
  flex-flow: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 60px;
  z-index: 99;

  transition: all;
  transition-duration: 500ms;
  transition-timing-function: ease;
  position: fixed;
}

.menu.hidden {
  opacity: 0;
  transform: translateY(8rem);
}

.menubars-svg {
  display: none;
}

#menubars-svg {
  display: none;
}

#show-menu-button {
  display: none;
}

.shoppingcart-svg {
  display: flex;
  flex-flow: row;
  justify-content: flex-start;
  fill: rgb(0, 179, 255);
  width: 25px;
  height: 25px;
  /* position: absolute; */
  left: 600px;
}

.shoppingcart-svg:hover {
  fill: rgb(121, 221, 255);
}

.cart-number {
  font-size: 0.9rem;
  position: absolute;
  right: 6.5%;
  top: 13%;
  background-color: rgb(255, 132, 0);
  width: 15px;
  height: 15px;
  border-radius: 50%;
  text-align: center;
}

.menu__ul {
  display: flex;
  flex-flow: row;
  justify-content: space-around;
  background-color: rgba(255, 255, 255, 0.8);
  width: 100%;
  height: 50px;

  /* ~~~~ FOR DEBUGGING~~~~*/
  /* border: 2px solid rgb(228, 152, 37); */
}

.li {
  width: 100%;
  height: 50px;
  border-right: 1px solid black;
  list-style: none;

  /* ~~~~ FOR DEBUGGING~~~~*/
  /* border: 2px solid rgb(46, 37, 228); */
}

.li:hover {
  background-color: rgba(255, 255, 255, 0.925);
  cursor: pointer;
}

.li.sticky:hover {
  background-color: rgba(212, 199, 171, 0.925);
}

a {
  display: flex;
  flex-flow: row;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 100%;
  height: 50px;
  color: black;
  font-size: 1.1rem;
  text-decoration: none;

  /* ~~~~ FOR DEBUGGING~~~~*/
  /* border: 2px solid rgb(40, 228, 37); */
}

.categories-list {
  display: flex;
  flex-flow: column;
  justify-content: space-evenly;
  text-align: center;
  list-style: none;
  border: 1px solid;
  position: relative;
  top: -20rem;
  background-color: rgba(250, 235, 215, 0.5);
  transition-property: all;
  transition-duration: 300ms;
  transition-timing-function: linear;
  opacity: 0;
}

.categories-list--active {
  opacity: 1;
  z-index: 99;
  transform: translateY(20rem);
}

.category-item {
  position: relative;
}

.category-item:hover {
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.5);
}

.logo {
  display: block;
  background-image: url(../imgs/tamar-logo.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 180px;
  height: 140px;
  position: relative;
  left: 5%;
  top: 70%;

  z-index: 0;
}

.modal {
  display: none;
  flex-flow: column wrap;
  align-items: center;

  width: 80vw;
  height: 80vh;
  background-color: rgba(255, 255, 255, 0.6);
  position: absolute;
  left: 100px;
  margin-top: 60px;
  padding-top: 10px;
  font-size: 2rem;
  text-align: center;
  background-image: url(../imgs/main-photo.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  z-index: 10;

  /* ~~~~ FOR DEBUGGING~~~~*/
  /* border: 3px solid red; */
}

.subscribenow-text {
  background-color: rgba(255, 255, 255, 0.662);
}

.subscribe-details {
  display: flex;
  flex-flow: row;
  align-items: center;

  margin-top: 30px;
  width: 30rem;

  /* ~~~~ FOR DEBUGGING~~~~*/
  /* border: 3px solid green; */
}

.subscribe-label {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  margin-right: 20px;
  font-size: 1.5rem;
  background-color: rgba(255, 255, 255, 0.662);
}

#subscribe-email {
  margin-right: 20px;
  width: 15rem;
  height: 1.5rem;
}

#submit-subscribe {
  cursor: pointer;
}

#subscribe-image {
  width: 40vw;
  height: 40vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}

.overlay {
  display: none;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  position: absolute;
  top: 0px;

  z-index: 9;

  /* ~~~~ FOR DEBUGGING~~~~*/
  /* border: 3px solid green; */
}

.close-modal,
.close-thanks {
  color: white;
  height: 40px;
  width: 40px;
  font-size: 1.5rem;
  background-color: rgba(255, 255, 255, 0);
  cursor: pointer;
  position: absolute;
  right: -2rem;
  top: -2rem;
  border: none;
}

.close-modal {
  position: ab;
}

h1 {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  margin: 30px 0px;
  width: 95%;
  font-size: 2.5rem;
}

.hide {
  display: none;
}

.thanks {
  /* display: flex;
    flex-flow: column wrap;
    align-items: center; */

  width: 40vw;
  height: 40vh;
  background-color: rgba(255, 255, 255, 0.8);
  position: absolute;
  top: 20%;
  margin-top: 60px;
  padding-top: 10px;
  font-size: 2rem;
  text-align: center;

  z-index: 10;

  /* border: 3px solid red; */
}

p {
  font-size: 1.3rem;
}

main {
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  padding-top: 15px;
  padding-bottom: 10px;

  /* ~~~~ FOR DEBUGGING~~~~*/
  /* border: 2px solid rgb(40, 228, 37); */
}

/* ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ */
/* ----------- Slider ---------- */
/* ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ */

.slider-container {
  display: flex;
  flex-flow: row;
  align-items: center;
  width: 95%;
  height: 65%;
  position: relative;
  /* top: 105%; */

  /* ~~~~ FOR DEBUGGING~~~~*/
  /* border: 3px solid rgb(0, 217, 255); */
}

.slider-wrapper .img-list .slider-image-item {
  height: 100%;
  object-fit: cover;
}

.slider-wrapper {
  display: flex;
  flex-flow: row;
  justify-content: center;
  width: 100%;
  height: 90%;
  overflow-x: hidden;

  /* ~~~~ FOR DEBUGGING~~~~*/
  /* border: 3px solid rgb(179, 0, 255); */
}

.img-list {
  display: flex;
  flex-flow: row;
  width: 100%;
  /* height: 300px; */
}

.slider-image-item {
  padding: 5px;
  /* width: calc(100% / 3); */
  width: 33%;
  /* height: 100%; */
  transition: transform 1s;

  /* ~~~~ FOR DEBUGGING~~~~*/
  /* border: 3px solid rgb(9, 255, 0); */
}

.slider_btns {
  position: absolute;
  top: 15%;
  height: 75%;
  width: 2.5%;
  background-color: rgba(255, 250, 218, 0.5);
  border: 0.5px solid rgba(198, 170, 118, 0.477);
  cursor: pointer;
  font-size: 2rem;
  color: rgb(108, 108, 108);
}

.slider-btn--left {
  left: 1.5%;
}

.slider-btn--right {
  right: 2.5%;
}

.slider_btns:hover {
  background-color: rgba(232, 232, 232, 0.6);
}

/* ----------- Slider end ----------------- */
/* ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ */

.large-imgs-container {
  display: flex;
  flex-flow: row;
  justify-content: center;
  align-items: center;
  width: 95%;
  margin-top: 50px;
  /* position: absolute; */
  top: 180%;
  box-shadow: 0px 0px 50px -10px rgba(0, 0, 0, 0.58);

  /* ~~~~ FOR DEBUGGING~~~~*/
  /* border: 3px solid red; */
}

.large-img-item {
  width: 50%;
  margin: 10px;

  /* ~~~~ FOR DEBUGGING~~~~*/
